.MovieList {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
}

.MovieList .list-container {
    width: 75%;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

.MovieList .header {
    border: 1px solid black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
}

.MovieList .header div {
    text-align: center;
    width: 33%;
}

.MovieList .header strong {
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.MovieList .header strong:hover {
    text-decoration: underline;
}

.MovieList .list-title {
    border: 1px solid black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: stretch;
        align-items: stretch;
    padding: 0.25rem 1rem 0.25rem 1rem
}

.MovieList .list-title .title {
    text-align: center;
    font-size: 24px;
    text-transform: uppercase;
    padding-bottom: 0;
}
.MovieList .stats {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 12px;
}

.MovieList .stats span {
    padding-left: 1rem;
}

.MovieList .list {
    list-style-type: none;
    margin-left: 0;
}

.MovieList .list>li:nth-child(even) {
    background-color: #e6e6e6;
}

.MovieList .list li+li {
    border-top: none;
}

.MovieList .person-image {
    position: fixed;
    margin-left: 70%;
    margin-top: 20vh;
    margin-bottom: 20vh;
    height: 60vh;
    width: 20%;
    border: 1px black solid;
    display: none;
}


